@font-face {
  font-family: 'martin-web';
  src: url('martin-web/MARTIN-Regular.eot'); /* IE9 Compat Modes */
  src: url('martin-web/MARTIN-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('martin-web/MARTIN-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('martin-web/MARTIN-Regular.woff') format('woff');
}

html, body {
  padding:0px;
  margin:0px;
  
}

* {
  box-sizing: border-box;
}

body { 
  background-color:black;
  color:white;
  font-family:monospace;
  color:#949393;
}

.desktop {
  display:block;
}

.mobile { 
  display:none;
}

@media screen and (max-width:600px) {

.desktop {
  display:none;
}

.mobile { 
  display:block;
}
 


}

a {
  color:white;
  color: #949393;
}

h1,h2,h3 { 
  font-weight:500;

}

header { 
  padding:0 15%;
  text-align:center;
  margin:0 auto;
}

h1 {
  font-family:"martin-web", sans-serif;
  font-size:220px;
  margin-bottom:40px;
  line-height: .7em;
  text-transform:uppercase;
  color:#505050;
  margin-top:0;
}

h2 {
  padding-top:30px;
  font-weight:normal;
  font-size:24px;
  line-height: 1.4em;
  
  
}

h3 {
  font-size:16px;
  line-height: 1.4em;
  padding-bottom: 70px;
}

.button { 
  border: 1px solid #949393;
  color: white;
  padding: 10px 5px;
  text-align: center;
  margin: 10px;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 100px;
  margin-top: 20px;
  font-family: "martin-web";
  font-size: 20px;
  z-index:1;
  background-color:rgba(0,0,0,.7);
}

.button:hover {
  background-color:#505050;
  border-color:#505050;
}

.button a:hover {
    color:black;
}

.button a {
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 30px;
  letter-spacing: .035em;
}

#side-button {
  position:fixed;
  bottom:120px;
  right:0;
  transform:rotate(-90deg);
  transform-origin: right bottom;
}

.date-exp {
  position: relative;
  right: 60%;
  text-align: right;
  padding-right: 20px;
  right: 60%;
  font-size: 20px;
  padding-top: 23px;
  color: #505050;
}
.namelist {
  max-width:1040px;
  margin:0 auto;


  &__entry {
    position:relative;
    min-height:20px;
    cursor:pointer;
    
  }

  &__line {
    position:absolute;
    left:40%;
    top:0px;
    height:100%;
    width:3px;
    background-color:#151414;
  }

  &__dot { 
   /* position:absolute;
    background-color:black;
    border-radius:40%;
    width:11px;
    height:11px;
    top:45px;
    left:-5px;
    border:2px solid black;*/
  }

  &__date {
    position:absolute;
    right: 60%;
    padding-right:20px;
    font-size:20px;
    padding-top:23px;
    color:#505050;
    text-align:right;
  }


  &__details {
    position:relative;
    left: 40%;
    padding-left:22px;
    font-size:14px;
    padding-bottom:50px;
    top:15px;
    max-width:40%;
    color:#505050;
  }

  &__name { 
    font-family:"martin-web";
    text-transform:uppercase;
    font-size:90px;
    color:#dde4e6;
    line-height:.7em;
    letter-spacing:.015em;
    word-spacing:.015em;
    padding-bottom:15px;
  }
  
  &__age {
    display:inline-block;
    padding-right:20px;
  }
  
  &__location {
    display:inline-block;
    
  }

  &__image {
    width:400px;
    height:140px;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: left top;

  }

}

@media screen and (max-width:600px) {
  
  .button a {
  padding: 5px 3px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: .035em;
  }
  
  header {
    padding:0 9%;
  }
  

  h1 {
  font-size:180px;  
  }
  
  h2 {
    font-size:18px;
  }
  
  h3 { font-size:15px;}
  
  .date-exp {
    right: 70%;
  }
  
  .namelist {

  &__entry {
  }

  &__line {
    left:30%;
  }

  &__date {
    right: 70%;
    padding-right:10px;
    font-size:18px;
  }
    
  &__details { 
    left: 30%;
    max-width:70%;
    padding-left:10px;
  }

    &__name {
      font-size:70px;
      line-height:.75em;
      word-wrap: break-word;  
    }


}

  
  
  
}